import { useEffect, useState, useRef } from "react";
import { useParams } from "react-router-dom";
import style from "./Detail.css";
import { useColor } from "color-thief-react";

function Detail() {
  const { id } = useParams();

  const [loading, setLoading] = useState(true);
  const [movies, setMovies] = useState(null);

  useEffect(() => {
    const getMovie = async () => {
      try {
        const response = await fetch(
          `https://yts.mx/api/v2/movie_details.json?movie_id=${id}`
        );
        const json = await response.json();
        setMovies(json.data.movie);
        //    setTextColorData(json.data.movie);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching movie:", error);
      }
    };

    getMovie();
  }, [id]);

  const {
    data: colors,
    loading: colorLoading,
    error: colorError,
  } = useColor(
    movies?.large_cover_image, // 이미지 URL
    "rgbString" // 추출된 색상 포맷
  );
  console.log(colors);
  return (
    <div>
      {loading ? (
        <h1>Loding....</h1>
      ) : (
        <div className="detail_wrap" style={{ backgroundColor: "#000" }}>
          <p></p>
          <div className="detail_contaner">
            <img className="detail_img" src={movies.large_cover_image} />
            <div
              className="detail_back_box"
              style={{
                backgroundImage: `url( ${movies.background_image_original}  )`,
              }}
            >
              <h2>{movies.title}</h2>
              <p>{movies.description_intro}</p>
              <ul>
                {movies.genres.map((lis) => (
                  <li key={lis}>{lis} </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default Detail;
